import useMyPositions from '@/hooks/liquidity/useMyPositions'
import useLiquidityStore from '@/store/liquidity'
import { Block } from '@cetus/design'
import { NoData } from '@cetus/ui-kit'
import { Button, Flex, Skeleton, VStack } from '@chakra-ui/react'
import { useNavigate } from 'react-router-dom'
import PositionItem from '../position/list/PositionItem'

function MyPositions({ priceDirect }: { priceDirect?: boolean }) {
  const navigate = useNavigate()
  const { apiPoolInfo } = useLiquidityStore()
  const { currentAccount, posBaseListLoading, data, rpcNodeErrorStr, onWalletModal } = useMyPositions()

  return (
    <VStack minW={{ base: '100%', lg: '1024px' }}>
      {!currentAccount?.address ? (
        <NoData type="nowallet" onboard={() => onWalletModal(true)} />
      ) : posBaseListLoading ? (
        [{}].map((item, index) => {
          return (
            <Block key={index} borderRadius="16px">
              <Skeleton height="4" width="100%" mb="16px" />
              <VStack w="100%">
                {[...Array(2)].map((_, index) => (
                  <Block key={index} bg="position_bg" borderRadius="16px" p="28px 16px">
                    <Flex w="100%" justify="space-between">
                      {[...Array(5)].map((_, index) => (
                        <Skeleton key={index} height="4" width="100px" />
                      ))}
                    </Flex>
                  </Block>
                ))}
              </VStack>
            </Block>
          )
        })
      ) : data && data?.length > 0 ? (
        <Block borderRadius="16px">
          <PositionItem poolInfo={{ list: data }} priceDirect={priceDirect} />
        </Block>
      ) : rpcNodeErrorStr ? (
        <NoData type="nodata" text={rpcNodeErrorStr} />
      ) : (
        <NoData
          type="nodata"
          text="You don't have positions for this pool"
          children={
            <Button
              borderRadius="8px"
              mt="8px"
              fontWeight="500"
              w="120px"
              h="28px"
              fontSize="12px"
              onClick={() => navigate(`/liquidity?tab=deposit&poolAddress=${apiPoolInfo?.poolAddress}`)}
            >
              Create Position
            </Button>
          }
        />
      )}
    </VStack>
  )
}

export default MyPositions
