import usePriceRange from '@/hooks/liquidity/usePriceRange'
import usePriceRangeStore from '@/store/liquidity/priceRange'
import { TickData } from '@/types'
import { Stack } from '@chakra-ui/react'
import PriceInput from './PriceInput'

export default function ControlPriceRange({
  perText,
  direct,
  minPriceData,
  maxPriceData,
  isFullRange = false
}: {
  perText: string
  direct: boolean
  minPriceData: Partial<TickData>
  maxPriceData: Partial<TickData>
  isFullRange?: boolean
}) {
  const { tickDataLoading } = usePriceRangeStore()
  const { handleAddPrice, handleSubPrice, setTickDataBasedOnPrice } = usePriceRange()
  return (
    <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" gap={{ base: '8px', lg: '16px' }}>
      <PriceInput
        title="Min Price"
        perText={perText}
        data={minPriceData}
        direct={direct}
        loading={tickDataLoading}
        handleAddPrice={handleAddPrice}
        handleSubPrice={handleSubPrice}
        setTickDataBasedOnPrice={setTickDataBasedOnPrice}
        isFullRange={isFullRange}
      />
      <PriceInput
        title="Max Price"
        perText={perText}
        data={maxPriceData}
        direct={direct}
        loading={tickDataLoading}
        handleAddPrice={handleAddPrice}
        handleSubPrice={handleSubPrice}
        setTickDataBasedOnPrice={setTickDataBasedOnPrice}
        isFullRange={isFullRange}
      />
    </Stack>
  )
}
