import usePriceRangeStore from '@/store/liquidity/priceRange'
import { Token } from '@cetus/types'
import { CoinPairImage, Icon } from '@cetus/ui-kit'
import { cancelBubble, formatPrice, textEllipses } from '@cetus/utils'
import { Button, HStack, Modal, ModalBody, ModalContent, ModalFooter, ModalHeader, ModalOverlay, Text, VStack } from '@chakra-ui/react'
import { useLayoutEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'

interface NFTModalProps {
  onClose: () => void
  posId: string
  tokenA: Token
  tokenB: Token
  fee: string
  isReverse: boolean
}
function NFTModal({ onClose, posId, tokenA, tokenB, fee, isReverse }: NFTModalProps) {
  const navigate = useNavigate()
  const [isDirect, setIsDirect] = useState(true)
  const { lowerTickData, upperTickData } = usePriceRangeStore()

  useLayoutEffect(() => {
    if (isReverse) {
      setIsDirect(false)
    }
  }, [isReverse])

  return (
    <Modal isOpen={true} autoFocus={false} returnFocusOnClose={false} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent w="400px">
        <ModalHeader>
          <HStack justify="space-between" w="100%">
            <Text fontSize="16px" fontWeight="500" color="text_caption">
              Open position successful
            </Text>
            <Button onClick={onClose} variant="unstyled" p="0" minW="unset" minH="unset" w="20px" h="20px">
              <Icon xlinkHref="#icon-icon_close" />
            </Button>
          </HStack>
        </ModalHeader>

        <ModalBody>
          <VStack gap="36px">
            <VStack
              gap="16px"
              bg="center / contain no-repeat url('/images/active-sui.gif') "
              w="240px"
              h="240px"
              justify="center"
              border="1px solid"
              borderColor="border"
              borderRadius="16px"
            >
              <CoinPairImage
                coinAIconUrl={tokenA?.logo_url}
                coinBIconUrl={tokenB?.logo_url}
                coinACoinType={tokenA?.coin_type}
                coinBCoinType={tokenB?.coin_type}
                w="48px"
                h="48px"
              />
              <Text fontSize="20px" fontWeight="500" color="text_caption" lineHeight="28px">
                {textEllipses(tokenA?.symbol, 10)} - {textEllipses(tokenB?.symbol, 10)}
              </Text>

              <HStack bg="bg_secondary" p="8px 12px" border="1px solid" borderColor="border" borderRadius="12px">
                <Text fontSize="14px">Fee tier</Text>
                <Text fontSize="14px" color="text_highlight">
                  {fee}
                </Text>
              </HStack>
            </VStack>
            <VStack w="100%" justify="center" borderRadius="8px" border="1px solid" borderColor="border" bg="input_bg" p="16px 8px">
              <Text color="text_caption" whiteSpace="nowrap">
                {isDirect ? formatPrice(lowerTickData?.price, 6) : formatPrice(upperTickData?.reversePrice, 6)} -&nbsp;
                {isDirect ? formatPrice(upperTickData?.price, 6) : formatPrice(lowerTickData?.reversePrice, 6)}
              </Text>
              <HStack>
                <HStack gap="0">
                  <Text color="primary_gray">
                    {textEllipses(isDirect ? (isReverse ? tokenA?.symbol : tokenB?.symbol) : isReverse ? tokenB?.symbol : tokenA?.symbol, 10)}
                  </Text>
                  <Text color="primary_gray">&nbsp;per&nbsp;</Text>
                  <Text color="primary_gray">
                    {textEllipses(isDirect ? (isReverse ? tokenB?.symbol : tokenA?.symbol) : isReverse ? tokenA?.symbol : tokenB?.symbol, 10)}
                  </Text>
                </HStack>
                <Icon
                  xlinkHref="#icon-icon_swap1"
                  w="16px"
                  h="16px"
                  onClick={(e: any) => {
                    cancelBubble(e)
                    setIsDirect(!isDirect)
                  }}
                />
              </HStack>
            </VStack>
          </VStack>
        </ModalBody>

        <ModalFooter p="20px 0 0">
          <Button flex="1" onClick={() => navigate(`/position-detail/${posId}`)} h="52px" borderRadius="12px" fontSize="16px" fontWeight="500">
            View Detail
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  )
}

export default NFTModal
