import useLiquidityStore from '@/store/liquidity'
import useDepositStore from '@/store/liquidity/deposit'
import { SelectTab } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d, formatPrice, isAvailableObject } from '@cetus/utils'
import { Center, Divider, HStack, Stack, StackProps, Text } from '@chakra-ui/react'
import { useEffect, useState } from 'react'

const dateTypeList = [
  {
    label: '24H',
    key: 'day'
  },
  {
    label: '7D',
    key: 'week'
  },
  {
    label: '30D',
    key: 'month'
  }
]

function PriceRangeForDate({ direct, wrapStyle = {} }: { direct: boolean; wrapStyle?: StackProps }) {
  const { priceRangeMap } = useDepositStore()
  const [tab, setTab] = useState({ type: '30D', key: 'month' })
  const { minPriceForDate, maxPriceForDate, setMinPriceForDate, setMaxPriceForDate } = useLiquidityStore()
  useEffect(() => {
    let _min = ''
    let _max = ''
    if (isAvailableObject(priceRangeMap)) {
      const lowest = (priceRangeMap as any)[tab?.key]?.[0]
      const lowValue = direct ? lowest : d(1).div(lowest).toString()
      const highest = (priceRangeMap as any)[tab?.key]?.[1]
      const highValue = direct ? highest : d(1).div(highest).toString()
      _min = formatPrice(lowValue, 6)
      _max = formatPrice(highValue, 6)
    } else {
      _min = '-'
      _max = '-'
    }
    setMinPriceForDate(_min)
    setMaxPriceForDate(_max)
  }, [direct, tab.key, priceRangeMap])

  const { isApp } = useWindowWidth()

  return (
    <HStack w="100%" justify="space-between" {...wrapStyle}>
      <Stack flexDir={{ base: 'column', lg: 'row' }} align={{ base: 'flex-start', lg: 'center' }}>
        <HStack>
          <Center borderRadius="4px" border="1px solid" borderColor="border" w="20px" h="20px" color="text_caption">
            <Divider w="14px" border="1px dashed" />
          </Center>
          <Text fontSize="12px" whiteSpace="nowrap">
            {tab?.type}&nbsp;Price Range
          </Text>
        </HStack>

        <Text color="text_caption" fontSize="12px" lineHeight="12px" h="12px">
          {direct ? minPriceForDate : maxPriceForDate}&nbsp;-&nbsp;
          {direct ? maxPriceForDate : minPriceForDate}
        </Text>
      </Stack>
      <SelectTab<any, any>
        type="outlineTab"
        tabList={dateTypeList}
        currentTab={tab}
        handleChangeTab={tab => {
          setTab({ type: tab.label, key: tab.key })
        }}
        isActive={(currentTab, tab) => currentTab.key === tab.key}
        wrapStyle={{
          h: isApp ? '28px' : '20px',
          p: '1px',
          border: '1px solid',
          borderColor: 'border',
          borderRadius: { base: '8px', lg: '4px' },
          gap: '0px',
          w: 'auto'
        }}
        itemStyle={{
          h: isApp ? '24px' : '16px',
          p: '2px 8px',
          fontSize: '12px',
          borderRadius: '4px',
          flex: 1
        }}
      />
    </HStack>
  )
}

export default PriceRangeForDate
