import TvlChart from '@/components/chart/TvlChart'
import VolumeChart from '@/components/chart/VolumeChart'
import useAnalyticChart, { ChartsTabsEnum, ChartsTabsType, DateTabsEnum, DateTypes } from '@/hooks/liquidity/useAnalyticChart'
import { SelectTab } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d, formatPrice, removeComma } from '@cetus/utils'
import { Box, HStack, Spinner, Text, VStack } from '@chakra-ui/react'

function AnalyticsCharts() {
  const { isApp } = useWindowWidth()
  const {
    handleChartTabChange,
    handleDateTabChange,
    defaultDisplay,
    chartsTabs,
    dateTypes,
    chartLoading,
    analyticsData,
    hoverData,
    setHoverData,
    currentChartTab,
    currentDateType,
    time
  } = useAnalyticChart()

  return (
    <VStack flex="1" border="1px solid" borderColor="border" borderRadius="16px" p={{ base: '8px', lg: '20px' }} bg="bg_secondary" gap="20px">
      <HStack w="100%" justify={{ base: 'center', lg: 'space-between' }}>
        <SelectTab<ChartsTabsType, ChartsTabsEnum>
          type="outlineTab"
          tabList={chartsTabs}
          currentTab={currentChartTab}
          handleChangeTab={handleChartTabChange}
          wrapStyle={{
            h: '32px',
            p: '3px',
            borderRadius: '8px',
            flex: { base: '1', lg: '0 0 308px' }
          }}
          itemStyle={{
            fontSize: '12px',
            flex: 1,
            borderRadius: '6px'
          }}
        />
        {!isApp && (
          <SelectTab<DateTypes, DateTabsEnum>
            type="outlineTab"
            tabList={dateTypes}
            currentTab={currentDateType}
            handleChangeTab={handleDateTabChange}
            wrapStyle={{
              h: '32px',
              p: '3px',
              borderRadius: '8px',
              flex: '0 0 128px'
            }}
            itemStyle={{
              fontSize: '12px',
              flex: 1,
              borderRadius: '6px'
            }}
          />
        )}
      </HStack>
      <VStack w="100%" align="flex-start">
        <Text color="text_caption" fontSize="24px">
          {hoverData?.num !== undefined && hoverData !== null
            ? d(removeComma(hoverData.num)).gte('0.01') || d(removeComma(hoverData.num)).equals('0')
              ? `$${formatPrice(hoverData?.num, 2)}`
              : '<$0.01'
            : defaultDisplay?.value}
        </Text>
        <Text>{hoverData?.date ? time : defaultDisplay?.title}</Text>
        {isApp && (
          <SelectTab<DateTypes, DateTabsEnum>
            type="outlineTab"
            tabList={dateTypes}
            currentTab={currentDateType}
            handleChangeTab={handleDateTabChange}
            wrapStyle={{
              mt: '8px',
              h: '32px',
              p: '3px',
              borderRadius: '8px',
              w: '128px'
            }}
            itemStyle={{
              fontSize: '12px',
              flex: 1,
              borderRadius: '6px'
            }}
          />
        )}
      </VStack>
      <Box w="100%" h="340px" mt="20px">
        <Box w="100%" h="100%" position="relative">
          {chartLoading && (
            <Box position="absolute" top="30%" left="50%" transform="translate(-50%,-50%)">
              <Spinner />
            </Box>
          )}
          {currentChartTab === ChartsTabsEnum.tvl ? (
            <TvlChart data={analyticsData} onChangeValue={data => setHoverData(data)} />
          ) : (
            <VolumeChart data={analyticsData} onChangeValue={data => setHoverData(data)} pageFrom="pools" />
          )}
        </Box>
      </Box>
    </VStack>
  )
}

export default AnalyticsCharts
