import ZapSubmiteInfo from '@/components/zap/ZapSubmiteInfo'
import useLiquidityStore from '@/store/liquidity'
import useAddLiquidityStore from '@/store/liquidity/addLiquidity'
import usePriceRangeStore from '@/store/liquidity/priceRange'
import { Block, TradeConfirmAmountInfo } from '@cetus/design'
import useNotifiStore from '@cetus/stores/src/notifi'
import { Token } from '@cetus/types'
import { CoinPairImage, Icon } from '@cetus/ui-kit'
import { formatNumberWithDown, textEllipses } from '@cetus/utils'
import {
  Box,
  Button,
  Divider,
  Heading,
  HStack,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Text,
  VStack
} from '@chakra-ui/react'
import { debounce } from 'lodash-es'
import { useMemo, useState } from 'react'
import DepositRatio from '../common/DepositRatio'
import TotalAmount from '../common/TotalAmount'
import PositionStatus from '../position/PositionStatus'
import RangeAlerts from '../position/details/RangeAlerts'

type AddLiquidityConfirmModalProps = {
  onClose: () => void
  onSubmit: () => void
  data: {
    feeTier?: {
      title: string
      feeDisplay: string
      feeRate: string
      poolAddress: string
      tvl: string
    }
    zapData?: {
      amount: string
      token: any
    }
  }
}

export default function AddLiquidityConfirmModal(props: AddLiquidityConfirmModalProps) {
  const {
    onClose,
    onSubmit,
    data: { feeTier, zapData }
  } = props
  const { fromToken, toToken, fromAmount, toAmount, totalAmount, percentMap, fromTokenLock, toTokenLock } = useAddLiquidityStore()
  const { lowerTickData, upperTickData } = usePriceRangeStore()
  const { currentPriceData, apiPoolInfo } = useLiquidityStore()
  const { setIsChecked } = useNotifiStore()
  const [direct, setDirect] = useState(true)
  const showReverse = useMemo(() => {
    if (apiPoolInfo?.tokenA?.coin_type === fromToken?.coin_type) {
      if (direct) {
        return false
      } else {
        return true
      }
    } else {
      if (direct) {
        return true
      } else {
        return false
      }
    }
  }, [apiPoolInfo, fromToken, toToken, direct])

  const perText = useMemo(() => {
    return direct
      ? `${textEllipses(toToken?.symbol, 10)} per ${textEllipses(fromToken?.symbol, 10)}`
      : `${textEllipses(fromToken?.symbol, 10)} per ${textEllipses(toToken?.symbol)}`
  }, [direct, fromToken?.symbol, toToken?.symbol])

  const onOk = debounce(
    () => {
      onSubmit()
      onClose()
    },
    1000,
    {
      leading: true,
      trailing: false
    }
  )

  const { notifiStatus } = useNotifiStore()
  return (
    <Modal
      isOpen={true}
      onClose={() => {
        onClose()
        setIsChecked(false)
      }}
      isCentered
    >
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>
          <Heading fontWeight="500" fontSize="16px">
            Add Liquidity
          </Heading>
        </ModalHeader>
        <ModalCloseButton />
        <ModalBody textAlign="center" p="0px">
          <VStack w="100%" gap="20px" p="8px 16px" pb="0px">
            <HStack w="100%" justifyContent="space-between" h="40px">
              <HStack>
                <CoinPairImage
                  coinACoinType={fromToken?.coin_type}
                  coinBCoinType={toToken?.coin_type}
                  coinAIconUrl={fromToken?.logo_url}
                  coinBIconUrl={toToken?.logo_url}
                  imageStyle={{
                    w: '40px',
                    h: '40px'
                  }}
                  imgBoxStyle={{
                    w: '40px',
                    h: '40px'
                  }}
                />
                <Text fontSize="16px" color="text_caption" textAlign="left">
                  {`${textEllipses(fromToken?.symbol, 10)} - ${textEllipses(toToken?.symbol, 10)}`}
                </Text>
              </HStack>

              <Block w="unset" p="8px 12px" borderRadius="12px" ml="4px">
                <HStack gap="12px">
                  <Text whiteSpace="nowrap">Fee Tier</Text>
                  <Text color="primary">{feeTier?.feeDisplay}</Text>
                </HStack>
              </Block>
            </HStack>

            {/* 交易数量展示 */}
            {zapData ? (
              <TradeConfirmAmountInfo
                coinA={{
                  amount: zapData?.amount as string,
                  ...(zapData?.token as Token)
                }}
              />
            ) : (
              <TradeConfirmAmountInfo
                coinA={{
                  amount: fromAmount as string,
                  ...(fromToken as Token)
                }}
                coinB={{
                  amount: toAmount as string,
                  ...(toToken as Token)
                }}
              />
            )}
            <Box w="100%">
              <HStack w="100%" justify="space-between" h="32px">
                <Text fontSize="16px" fontWeight="500" color="text_caption">
                  Selected Range
                </Text>
                <PositionStatus isActive={!fromTokenLock && !toTokenLock} isRow imgStyle={{ imgW: '29px', imgH: '16px' }} w="105px" h="32px" />
              </HStack>
              <VStack gap="0" border="1px solid" bg="bg_secondary" borderColor="border" borderRadius="12px" mt="8px">
                <HStack justify="center" h="48px" w="100%" gap="4px">
                  <Text>Current Price</Text>
                  <Text as="span" color="text_caption">
                    {formatNumberWithDown(showReverse ? currentPriceData.reverseCurrentPrice : currentPriceData?.currentPrice, 6)}
                  </Text>
                  <Text>{perText}</Text>
                  <Button variant="unstyled" onClick={() => setDirect(!direct)}>
                    <Icon xlinkHref="#icon-icon_swap1" />
                  </Button>
                </HStack>
                <Box h="1px" w="calc(100% - 32px)" p="0 12px" bg="border" />
                <HStack w="100%" h="100px">
                  <PriceItem
                    title="Min Price"
                    price={formatNumberWithDown(showReverse ? upperTickData?.reversePrice : lowerTickData?.price, 6)}
                    perText={perText}
                  />
                  <Divider orientation="vertical" h="40px" />
                  <PriceItem
                    title="Max Price"
                    price={formatNumberWithDown(showReverse ? lowerTickData?.reversePrice : upperTickData?.price, 6)}
                    perText={perText}
                  />
                </HStack>
              </VStack>
            </Box>

            {zapData ? (
              <ZapSubmiteInfo action="Deposit" inConfirmModal={true} />
            ) : (
              <>
                <DepositRatio
                  tokenA={fromToken}
                  tokenB={toToken}
                  isReverse={fromToken?.coin_type !== apiPoolInfo?.tokenA?.coin_type}
                  type="text"
                  percentMap={percentMap}
                />
                <TotalAmount loading={false} totalAmount={totalAmount} />

                {!fromTokenLock &&
                  !toTokenLock &&
                  !(lowerTickData?.price === '0' && upperTickData?.price === '∞') &&
                  notifiStatus == 'authenticated' && (
                    <RangeAlerts
                      subscriptionSource="AddLiquidity"
                      title="Range alerts for this position"
                      description="Subscribe to receive alerts on email, SMS, or Telegram when the price for this pool moves out of your selected range."
                      wrapStyle={{
                        bg: 'bg_secondary',
                        borderRadius: '12px'
                      }}
                    />
                  )}
              </>
            )}
            <Button w="calc(100% + 32px)" bottom="0px" mt="4px" h="52px" borderRadius="12px" fontSize="16px" fontWeight="500" onClick={onOk}>
              Add Liquidity
            </Button>
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}

const PriceItem = ({ title, price, perText }: { title: string; price: string; perText: string }) => {
  return (
    <VStack flex="1">
      <Text>{title}</Text>
      <Text color="text_caption">{price}</Text>
      <Text>{perText}</Text>
    </VStack>
  )
}
