import { HStack, StackProps, Text, VStack } from '@chakra-ui/react'
import React from 'react'

interface DivisionBlockProps {
  children: React.ReactNode[]
  title: string
  wrapStyle?: StackProps
}

function DivisionBlock({ children, title, wrapStyle = {} }: DivisionBlockProps) {
  return (
    <VStack gap="8px" p="10px 16px 16px" border="1px solid" borderColor="border" borderRadius="16px" bg="bg_six" {...wrapStyle}>
      <HStack w="100%" justify="space-between" h="32px">
        <Text fontSize="16px" fontWeight="500" color="text_caption">
          {title}
        </Text>
        {children?.[0]}
      </HStack>
      {children?.[1]}
    </VStack>
  )
}

export default DivisionBlock
