import useRecentTransactions from '@/hooks/liquidity/useRecentTransactions'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { H5MapTable, NoData, Table } from '@cetus/ui-kit'
import { Button, Center, HStack, Spinner, Text, VStack } from '@chakra-ui/react'
import { getColumns } from './components'
import { DataItem } from './type'

function RecentTransactions() {
  const { getExplorerUrl } = useExplorer()

  const { isLoading, list, apiPoolInfo, currentPage, onCurrentPageChange, hasNextPage } = useRecentTransactions()
  const { isApp } = useWindowWidth()

  return (
    <VStack w="100%" position="relative" mt={{ base: '12px', lg: '24px' }} gap="0px">
      <HStack w="100%" justify="space-between">
        <Text fontSize="24px" color="text_caption" fontWeight="500">
          Transactions
        </Text>
      </HStack>
      {!isLoading && list?.length == 0 ? (
        <NoData type="nodata" mt="16px" text="No Recent Transactions" />
      ) : isApp ? (
        <H5MapTable<DataItem>
          columns={getColumns(getExplorerUrl, apiPoolInfo?.isReverse)}
          dataSource={list}
          loading={isLoading && currentPage === 1}
          itemSkeletonLength={5}
          itemHeight="30px"
          wrapStyle={{ gap: 0 }}
          rowStyle={(_, index) => ({
            w: '100%',
            p: '0px',
            mt: '20px'
          })}
          onRowClick={(item: any) => {
            window.open(getExplorerUrl(item?.tx, 'tx'), '_blank')
          }}
        />
      ) : (
        <Table<DataItem>
          columns={getColumns(getExplorerUrl, apiPoolInfo?.isReverse)}
          dataSource={list}
          skeletonLength={10}
          loading={isLoading && currentPage === 1}
          onRowClick={(item: any) => {
            window.open(getExplorerUrl(item?.tx, 'tx'), '_blank')
          }}
          rowStyle={{ h: '52px', cursor: 'pointer' }}
        />
      )}
      {hasNextPage && (
        <Center mt={{ base: '40px', lg: '20px' }}>
          <Button
            w="120px"
            h="32px"
            fontSize="12px"
            borderRadius="8px"
            bg="button_ghost_bg"
            color="primary"
            borderColor="border"
            variant="outline"
            onClick={onCurrentPageChange}
          >
            {isLoading ? <Spinner size="sm" color="text_caption" /> : 'Load More'}
          </Button>
        </Center>
      )}
    </VStack>
  )
}

export default RecentTransactions
