import usePoolInfo from '@/hooks/liquidity/usePoolInfo'
import useLiquidityStore from '@/store/liquidity'
import useTvlInfoStore from '@/store/liquidity/liquidityTvl'
import { AddressCopyLink } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import { Token } from '@cetus/types'
import { HTextLabelBox, SingleCoinImage } from '@cetus/ui-kit'
import { addComma, formatPrice, fromDecimalsAmountFix, textEllipses } from '@cetus/utils'
import { Box, Grid, Heading, HStack, Progress, Skeleton, Text, VStack } from '@chakra-ui/react'

const getRatio = (value: string) => {
  if (value === '--') return '--'
  return `${value}%`
}

function PoolInfo() {
  const { apiPoolInfo, contractPoolInfo } = useLiquidityStore()
  const { getExplorerUrl } = useExplorer()
  const { poolAddress } = useQueryParams()
  const { tokenAAmount, tokenBAmount, tokenAAmountUSD, tokenBAmountUSD, tvlLoading } = useTvlInfoStore()
  const { tokenARatio, tokenBRatio } = usePoolInfo()
  return (
    <VStack
      w={{ base: '100%', lg: '420px', xl: '440px' }}
      gap={{ base: '20px', lg: '40px' }}
      p={{ base: '20px 8px 8px', lg: '20px 16px' }}
      borderRadius="16px"
      border="1px solid"
      borderColor="border"
      bg="bg_secondary"
      minW={{ base: '100%', lg: '420px' }}
      maxW={{ base: '100%', lg: '440px' }}
    >
      <VStack gap="20px" w="100%">
        <HStack w="100%" justify="space-between">
          <Heading fontSize="16px" fontWeight="400">
            Pool Info
          </Heading>
        </HStack>
        <Grid templateColumns={{ base: 'repeat(1, 1fr)', lg: 'repeat(2, 1fr)' }} w="100%" gap={{ base: '20px', lg: '20px 36px' }}>
          <HTextLabelBox
            label="Pool Address"
            labelStyle={{
              fontSize: '14px',
              whiteSpace: 'nowrap'
            }}
            value={
              <AddressCopyLink
                address={poolAddress || ''}
                fontSize="14px"
                color="text_caption"
                onClickLink={() => window.open(getExplorerUrl(poolAddress, 'poolAddress'))}
              />
            }
            valueStyle={{
              fontSize: '14px'
            }}
          />
          <HTextLabelBox
            label="Tick Spacing"
            labelStyle={{
              whiteSpace: 'nowrap',
              fontSize: '14px'
            }}
            value={contractPoolInfo?.tickSpacing || ''}
            valueStyle={{
              fontSize: '14px'
            }}
          />

          <HTextLabelBox
            label="Pool ID"
            labelStyle={{
              fontSize: '14px'
            }}
            value={contractPoolInfo?.index || apiPoolInfo?.index ? `#${contractPoolInfo?.index || apiPoolInfo?.index}` : '--'}
            valueStyle={{
              fontSize: '14px'
            }}
          />

          <HTextLabelBox
            label="Fee Tier"
            labelStyle={{
              fontSize: '14px'
            }}
            value={`${apiPoolInfo?.feeDisplay}` || '--'}
            valueStyle={{
              fontSize: '14px'
            }}
          />
        </Grid>
      </VStack>
      <VStack gap="20px" w="100%">
        <HStack justify="flex-start" w="100%">
          <Heading fontSize="16px" fontWeight="400">
            Pool Composition
          </Heading>
        </HStack>
        <VStack gap="16px" w="100%">
          <HStack w="100%" justify="space-between">
            <HStack gap="12px">
              <SingleCoinImage
                imageUrl={apiPoolInfo?.displayTokenA?.logo_url}
                w="28px"
                h="28px"
                coinType={apiPoolInfo?.displayTokenA ? apiPoolInfo?.displayTokenA?.coin_type : ''}
              />
              <VStack align="flex-start" gap="4px">
                <Text fontSize="16px" fontWeight="500" color="text_caption">
                  {textEllipses(apiPoolInfo?.displayTokenA?.symbol, 10)}
                </Text>
                {tvlLoading ? (
                  <Skeleton h="12px" w="46px" />
                ) : (
                  <Text fontSize="12px" fontWeight="500">
                    {apiPoolInfo?.isReverse ? getRatio(tokenBRatio) : getRatio(tokenARatio)}
                  </Text>
                )}
              </VStack>
            </HStack>
            <Box w="200px">
              <Progress
                value={Number(apiPoolInfo?.isReverse ? tokenBRatio : tokenARatio)}
                size="xs"
                h="4px"
                borderRadius="4px"
                bg={[tokenARatio, tokenBRatio].includes('--') ? 'border' : 'quote_green'}
                sx={{
                  'div[role="progressbar"]': {
                    bg: [tokenARatio, tokenBRatio].includes('--') ? 'background' : 'primary'
                  }
                }}
              />
            </Box>
            <HStack gap="12px">
              <VStack align="flex-end" gap="4px">
                <Text fontSize="16px" fontWeight="500" color="text_caption">
                  {textEllipses(apiPoolInfo?.displayTokenB?.symbol, 10)}
                </Text>
                {tvlLoading ? (
                  <Skeleton h="12px" w="46px" />
                ) : (
                  <Text fontSize="12px" fontWeight="500">
                    {apiPoolInfo?.isReverse ? getRatio(tokenARatio) : getRatio(tokenBRatio)}
                  </Text>
                )}
              </VStack>
              <SingleCoinImage
                imageUrl={apiPoolInfo?.displayTokenB?.logo_url}
                w="28px"
                h="28px"
                coinType={apiPoolInfo?.displayTokenB ? apiPoolInfo?.displayTokenB?.coin_type : ''}
              />
            </HStack>
          </HStack>
          <VStack w="100%" flexDir={apiPoolInfo?.isReverse ? 'column-reverse' : 'column'} gap="16px">
            <TokenCard loading={tvlLoading} token={apiPoolInfo?.tokenA} amount={tokenAAmount} amountUSD={tokenAAmountUSD} />
            <TokenCard loading={tvlLoading} token={apiPoolInfo?.tokenB} amount={tokenBAmount} amountUSD={tokenBAmountUSD} />
          </VStack>
        </VStack>
      </VStack>
    </VStack>
  )
}

interface TokenCardProps {
  token?: Token
  amount?: string
  amountUSD: string
  loading: boolean
}

const TokenCard = ({ token, amount, amountUSD, loading }: TokenCardProps) => {
  const { getExplorerUrl } = useExplorer()
  return token ? (
    <VStack w="100%" p={{ base: '16px 8px', lg: '16px 20px' }} borderRadius="8px" bg="background" border="1px solid" borderColor="border" gap="16px">
      <HStack w="100%" justify="space-between">
        <HStack gap="12px">
          <SingleCoinImage imageUrl={token?.logo_url} w="28px" h="28px" coinType={token ? token?.coin_type : ''} />
          <VStack align="flex-start" gap="4px">
            <Text color="text_caption" fontSize="16px">
              {textEllipses(token?.symbol, 10)}
            </Text>
            <Text fontSize="12px">{token?.name}</Text>
          </VStack>
        </HStack>
        <AddressCopyLink
          address={token?.coin_type || ''}
          fontSize="14px"
          color="text_caption"
          onClickLink={() => window.open(getExplorerUrl(token?.coin_type, 'coin'))}
        />
      </HStack>
      <HTextLabelBox
        label="Amount"
        labelStyle={{
          fontSize: '14px'
        }}
        value={addComma(fromDecimalsAmountFix(amount || '0', token.decimals)) as string}
        valueStyle={{
          fontSize: '14px'
        }}
        isLoading={loading}
        wrapStyle={{ h: '14px' }}
        skeletonStyle={{ valueW: '120px' }}
      />
      <HTextLabelBox
        label="Amount (USD)"
        labelStyle={{
          fontSize: '14px'
        }}
        value={amountUSD && amountUSD !== '--' ? `$${formatPrice(amountUSD)}` : '--'}
        valueStyle={{
          fontSize: '14px'
        }}
        isLoading={loading}
        wrapStyle={{ h: '14px' }}
        skeletonStyle={{ valueW: '120px' }}
      />
    </VStack>
  ) : null
}

export default PoolInfo
