import Slippage from '@/components/common/Slippage'
import PoolsAnalytics from '@/components/liquidity/analytics/PoolsAnalytics'
import MyPositions from '@/components/liquidity/MyPositions'
import PoolsStats from '@/components/liquidity/PoolsStats'
import ProvideLiquidity from '@/components/liquidity/provideLiquidity'
import { SelectToken } from '@/components/liquidity/SelectPool'
import SelectTokenAnddFeeConfirm from '@/components/liquidity/SelectTokenAndFeeConfirm'
import useGetTvlInfo from '@/hooks/liquidity/useGetTvlInfo'
import useLiquidity from '@/hooks/liquidity/useLiquidity'
import useGlobalStore from '@/store/common/global'
import useLiquidityStore from '@/store/liquidity'
import { isTrustedToken } from '@/utils'
import { CetusTooltip, CurrentPrice, FeeTierBlock, SelectTab } from '@cetus/design'
import { FeeTier } from '@cetus/design/src/components/common/FeeTierSelect'
import WarningTokenTipsModal from '@cetus/design/src/components/common/WarningTokenTipModal'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { BackButton, Icon, NoData, RefreshButton } from '@cetus/ui-kit'
import { formatNumberWithDown } from '@cetus/utils'
import { Box, Button, Center, HStack, Spinner, Stack, Text, useDisclosure, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { TabsEnum } from '../hooks/liquidity/useLiquidityInteraction'

function Liquidity() {
  const navigate = useNavigate()
  const { apiPoolInfo, apiPoolInfoLoading, currentPriceData, netError } = useLiquidityStore()
  const { poolAddress, from, to } = useQueryParams()
  const {
    feeTierList,
    currentFeeTier,
    rangeTabList,
    quoteWhiteTokenList,
    warningTokenList,
    currentTab,
    setCurrentTab,
    tabList,
    favoriteStyle,
    onChangeFavorites,
    selectedTokenA,
    selectedTokenB,
    onConfirm,
    setFeeTier,
    handleRefresh,
    handleGetPrice,
    onJump2Swap,
    handleSelectToken,
    getSelectTokenProps
  } = useLiquidity()

  const { isApp } = useWindowWidth()

  const { isOpen, onOpen, onClose } = useDisclosure()

  const { totalAmountUSD: totalTvl, tvlLoading } = useGetTvlInfo()

  const showContent = poolAddress && apiPoolInfo?.tokenA && apiPoolInfo?.tokenB

  const { tooltip, xlinkHref, svgFill, svgHover } = favoriteStyle

  const [selectTokenAProps, setSelectTokenAProps] = useState<any>({})
  const [selectTokenBProps, setSelectTokenBProps] = useState<any>({})

  const fetchSelectTokenProps = async () => {
    const tokenAProps = await getSelectTokenProps(from, selectedTokenA)
    const tokenBProps = await getSelectTokenProps(to, selectedTokenB)
    setSelectTokenAProps(tokenAProps)
    setSelectTokenBProps(tokenBProps)
  }
  useEffect(() => {
    fetchSelectTokenProps()
  }, [from, to, selectedTokenA, selectedTokenB])
  const { backUrl } = useGlobalStore()
  const [priceDirect, setPriceDirect] = useState(true)
  return (
    <>
      <Box w="100%" mt="20px">
        <HStack w="100%" minW={{ base: '100%', lg: '1024px' }} justify="space-between">
          {/* <BackButton text="Pools" onClick={() => navigate('/pools')} /> */}
          <BackButton text="Back" onClick={() => (backUrl ? navigate(backUrl) : navigate('/pools'))} />
          {showContent && (
            <HStack gap="8px">
              <CetusTooltip placement="top" tooltip={tooltip}>
                <Button
                  variant="outline"
                  w="32px"
                  h="32px"
                  lineHeight="32px"
                  onClick={onChangeFavorites}
                  p="0 6px"
                  borderRadius="8px"
                  color="text_paragraph"
                  bg="background"
                  display="flex"
                  justifyContent="center"
                  gap="4px"
                  sx={{
                    _hover: {
                      color: 'text_caption',
                      svg: {
                        fill: svgHover
                      }
                    }
                  }}
                >
                  <Icon xlinkHref={xlinkHref} svgFill={svgFill} svgHover={svgHover} fontSize="18px" />
                </Button>
              </CetusTooltip>
              <CetusTooltip placement="top" tooltip="Trade">
                <Button
                  variant="outline"
                  bg="background"
                  h="32px"
                  w="32px"
                  onClick={onJump2Swap}
                  borderRadius="8px"
                  p="0 6px"
                  color="text_paragraph"
                  display="flex"
                  justifyContent="center"
                  gap="4px"
                  sx={{
                    _hover: {
                      color: 'text_caption',
                      svg: {
                        fill: 'text_caption'
                      }
                    }
                  }}
                >
                  <Icon xlinkHref="#icon-icon_swap1" fontSize="16px" />
                </Button>
              </CetusTooltip>
              <CetusTooltip placement="top" tooltip="Refresh">
                <Box>
                  <RefreshButton
                    key={poolAddress}
                    handleRefresh={handleRefresh}
                    isAutoRefresh
                    refreshInterval={60}
                    innerStyle={{ bg: 'background' }}
                    w="32px"
                    h="32px"
                    borderRadius="8px"
                    bg="background"
                  />
                </Box>
              </CetusTooltip>
            </HStack>
          )}
        </HStack>
        <Stack
          flexDir={{ base: 'column', lg: 'row' }}
          w="100%"
          minW={{ base: '100%', lg: '1024px' }}
          justify="space-between"
          mt="20px"
          gap={{ base: '20px', lg: '8px' }}
        >
          <Stack flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '20px', lg: '8px' }} align={{ base: 'flex-start', lg: 'center' }}>
            <HStack gap="12px">
              <SelectToken
                onChange={token => {
                  handleSelectToken(token, selectedTokenA, selectedTokenB, from, to, true)
                }}
                {...selectTokenAProps}
              />
              <SelectToken
                onChange={token => {
                  handleSelectToken(token, selectedTokenB, selectedTokenA, to, from, false)
                }}
                {...selectTokenBProps}
              />
            </HStack>
            <FeeTierBlock
              value={currentFeeTier as FeeTier}
              loading={!currentFeeTier && apiPoolInfoLoading}
              feeTierList={feeTierList}
              disabled={netError}
              onChange={item => {
                if (item?.poolAddress) {
                  navigate(`/liquidity?poolAddress=${item?.poolAddress}`)
                } else {
                  setFeeTier(item)
                  onOpen()
                }
              }}
            />
          </Stack>
          {showContent && (
            <PoolsStats apiPoolInfo={apiPoolInfo} totalTvl={totalTvl} apiPoolInfoLoading={apiPoolInfoLoading} tvlLoading={tvlLoading} />
          )}
        </Stack>
        {showContent ? (
          <>
            <Stack
              w="100%"
              flexDir={{ base: 'column', lg: 'row' }}
              justify={{ base: 'center', lg: 'space-between' }}
              align={{ base: 'flex-start', lg: 'center' }}
              minW={{ base: '100%', lg: '1024px' }}
            >
              <SelectTab<any, any>
                type="borderTab"
                tabList={tabList}
                currentTab={currentTab}
                handleChangeTab={item => {
                  const key = Object.keys(TabsEnum).find(tab => TabsEnum[tab as keyof typeof TabsEnum] === item.label)
                  if (poolAddress) {
                    navigate(`/liquidity?tab=${key}&poolAddress=${poolAddress}`)
                  }
                  setCurrentTab(key as keyof typeof TabsEnum)
                }}
                wrapStyle={{
                  w: { base: '100%', lg: 'auto' },
                  justify: { base: 'space-between', lg: 'flex-start' },
                  p: { base: '0 12px', lg: '0' },
                  bg: { base: 'bg_secondary', lg: 'transparent' },
                  h: isApp ? '60px' : '58px',
                  gap: { base: '8px', lg: '45px' },
                  border: { base: '1px solid', lg: 'none' },
                  borderColor: { base: 'border', lg: 'transparent' },
                  borderRadius: { base: '16px', lg: 'unset' },
                  mt: { base: '20px', lg: '12px' }
                }}
                itemStyle={{
                  fontSize: isApp ? '14px' : '16px',
                  fontWeight: '500'
                }}
              />

              {currentTab === 'deposit' && !isApp && (
                <Box mt="24px">
                  <Slippage />
                </Box>
              )}
              {currentTab !== 'deposit' && apiPoolInfo && (
                <Stack
                  w={isApp ? '100%' : 'auto'}
                  mt={{ base: '4px', lg: '12px' }}
                  gap="12px"
                  flexDir={{ base: 'column', lg: 'row' }}
                  justify={{ base: 'center', lg: 'space-between' }}
                  align={{ base: 'flex-start', lg: 'center' }}
                >
                  <Text>Current Price</Text>
                  <CurrentPrice
                    noCenter={isApp ? true : false}
                    fromToken={apiPoolInfo?.displayTokenA}
                    toToken={apiPoolInfo?.displayTokenB}
                    fromValue="1"
                    toValue={formatNumberWithDown(
                      apiPoolInfo?.isReverse ? currentPriceData?.reverseCurrentPrice : currentPriceData?.currentPrice,
                      6,
                      true
                    ).toString()}
                    color="text_caption"
                    wrapStyle={isApp ? { w: '100%' } : {}}
                    handlePageToggleDirect={() => setPriceDirect(!priceDirect)}
                  />
                </Stack>
              )}
            </Stack>
            <Box h={{ base: '20px', lg: '8px' }} />
            {currentTab === 'deposit' && (
              <ProvideLiquidity
                rangeTabList={rangeTabList as any}
                getList={handleRefresh}
                getPrice={handleGetPrice}
                currentFeeTier={currentFeeTier as any}
              />
            )}
            {currentTab === 'positions' && <MyPositions priceDirect={priceDirect} />}
            {currentTab === 'analytics' && <PoolsAnalytics />}
          </>
        ) : (
          <Box mt="20px">
            {!apiPoolInfo && apiPoolInfoLoading ? (
              <Center w="100%" h="480px" background="bg_secondary" borderRadius="12px" border="1px solid" borderColor="border">
                <Spinner />
              </Center>
            ) : (
              <NoData type="custom" imgUrl="/images/img_create_pool.png" h="480px">
                <VStack>
                  {selectedTokenA && selectedTokenB ? (
                    isTrustedToken(selectedTokenA, quoteWhiteTokenList) || isTrustedToken(selectedTokenB, quoteWhiteTokenList) ? (
                      <>
                        <Text h="20px" lineHeight="20px" fontSize="14px" color="text_caption" fontWeight="500">
                          This Pool has not been initialized
                        </Text>
                        <Text fontSize="14px">Do you want to initialize it?</Text>
                        <Button
                          mt="12px"
                          w="120px"
                          h="32px"
                          borderRadius="8px"
                          colorScheme="blue"
                          fontSize="14px"
                          fontWeight="500"
                          onClick={() => {
                            onClose()
                            onConfirm()
                          }}
                        >
                          Initialized Pool
                        </Button>
                      </>
                    ) : (
                      <Text h="20px" lineHeight="20px" fontSize="14px" color="text_caption">
                        No liquidity data
                      </Text>
                    )
                  ) : (
                    <Text h="20px" lineHeight="20px" fontSize="14px" color="text_caption">
                      Select the token you want to provide liquidity for.
                    </Text>
                  )}
                </VStack>
              </NoData>
            )}
          </Box>
        )}
      </Box>
      {/* Token 警告弹窗 */}
      {apiPoolInfo?.displayTokenA && apiPoolInfo?.displayTokenB && (
        <WarningTokenTipsModal
          tokensInfo={warningTokenList}
          waringModalCancel={(tokenInfo: Token[]) => {
            navigate('/pools')
          }}
        />
      )}
      <SelectTokenAnddFeeConfirm
        title="This Pool has not been initialized"
        subTitle="Do you want to initialize it?"
        btnText="Initialized Pool"
        isOpen={isOpen}
        onClose={onClose}
        onConfirm={onConfirm}
      />
    </>
  )
}

export default Liquidity
